/************************************** content title style ***************************************/
article {
	.kratos-entry-header {
		+ {
			.kratos-post-content {
				h1 {
					padding-bottom: 20px;
					transition: all 0.3s ease-in-out;
					border-bottom: 1px solid #51aded;
					&:before {
						font: normal normal normal 100% FontAwesome;
						margin-right: 10px;
						color: #51aded;
						content: "\f0c1";
						line-height: 1;
					}
					&:hover {
						color: #51aded;
					}
				}
				h2 {
					padding-bottom: 20px;
					transition: all 0.3s ease-in-out;
					border-bottom: 1px solid #51aded;
